(function() {
  'use strict';

  angular
    .module('ppulse', ['ngAnimate', 'ngCookies', 'ngMaterial', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr', 'ngStorage']);

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('VideoDetailsController', VideoDetailsController);

  /** @ngInject */
  function VideoDetailsController(authService, videoService, $stateParams) {
    var vm = this,
        user = angular.copy(authService.getUser()),
        randomVideoIndex = Math.floor(Math.random() * 20) + 1;

    // Get video by Video Id
    videoService.getVideo(user.sessionId, $stateParams.videoId).then(function(res){
      vm.video = res.data.data;
    }).catch(function(error){
      if(error.statusText === 'Unauthorized'){
        $state.go('login');
      }
      console.log(error);
    });


    // Get Recommended videos
    videoService.getVideoListing(user.sessionId, randomVideoIndex, 3).then(function(res){
      vm.videos = res.data.data;
    }).catch(function(error){
      console.log(error);
    });

  }
})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(toastr, $log, $state, $stateParams, authService, $mdDialog, config, $timeout) {
    var vm = this;

    vm.login = login;

    vm.processing = false;
    vm.errorMsg = '';
    vm.credentials = {};

    function login() {
      vm.processing = true;
      authService.login(vm.credentials).success(function(res){
        vm.processing = false;
        if(res.error){
          vm.errorMsg = res.error;
        } else {
          authService.setUser(res);
          $state.go('home');
        }

      }).error(function(err){
        $log.log(err);
      });
    }


  }
})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController(authService, videoService, $state) {
    var vm = this,
        user = angular.copy(authService.getUser()),
        lazyload = 0,
        numberOfVideos = 12;

    vm.loadMore = loadMore;
    vm.videos = [];

    getVideoListing();

    function getVideoListing() {
      videoService.getVideoListing(user.sessionId, lazyload*numberOfVideos, numberOfVideos).then(function(res){
        vm.videos = vm.videos.concat(res.data.data);
      }).catch(function(error){
        if(error.statusText === 'Unauthorized'){
          $state.go('login');
        }
        console.log(error);
      });
    }

    function loadMore() {
      lazyload++;
      getVideoListing();
    }

  }
})();

(function () {
  angular.module('ppulse')
    .service('videoService', function ($http, config) {

      this.getVideoListing = function (sessionId, skip, limit) {
        var req = {
          method: 'GET',
          url: config.apiUrl + '/videos?sessionId='+ sessionId + '&skip=' + skip + '&limit=' + limit
        };
        return $http(req);
      };

      this.getVideo = function (sessionId, videoId) {
        var req = {
          method: 'GET',
          url: config.apiUrl + '/video?sessionId='+ sessionId + '&videoId=' + videoId
        };
        return $http(req);
      };

      this.rateVideo = function (sessionId, videoId, rating) {
        var req = {
          method: 'POST',
          data: {
            "videoId": videoId,
            "rating": rating
          },
          url: config.apiUrl + '/video/ratings?sessionId='+ sessionId
        };
        return $http(req);
      };


    });
})();

(function () {
  angular.module('ppulse')
    .service('authService', function ($http, config,$localStorage) {
      var user = {};
      this.login = function (credentials) {
        return $http.post(config.apiUrl + '/user/auth', credentials);
      };

      this.logout = function (sessionId) {
        $localStorage.user = undefined;
        var req = {
          method: 'GET',
          url: config.apiUrl + '/user/logout?sessionId='+sessionId
        };
        return $http(req);
      };

      this.setUser = function(userDetails) {
        $localStorage.user = angular.copy(userDetails);
      };

      this.getUser = function() {
        return $localStorage.user;
      };
    });
})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('rating', rating);

  /** @ngInject */
  function rating(videoService, authService) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/rating/rating.html',
      scope: {
          rate: '=',
          videoId: '='
      },
      controller: RatingController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function RatingController($scope, $log, $state, authService, toastr) {
      var vm = this,
          user = angular.copy(authService.getUser());
        vm.avgRating = 0;

        caculateAvgRate();

        // Rate Video method
        vm.rateVideo = function(index) {
          videoService.rateVideo(user.sessionId, vm.videoId, index+1).then(function(res){
            vm.rate = res.data.data.ratings;
            caculateAvgRate();
            toastr.success('Video rating saved successfully!');
          }).catch(function(error){
            console.log(error);
          });
        };

        // Calculating Average and rounding off with floor to display number of stars
        function caculateAvgRate() {
          var sum = 0;
          for(var i = 0;i < vm.rate.length;i++){
            sum += vm.rate[i];
          }
          vm.avgRating = Math.floor(sum/vm.rate.length);
        }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($window, $scope, $log, $state, authService, toastr, $location, $anchorScroll) {
      var vm = this,
          user = angular.copy(authService.getUser());


      // Logout method
      vm.logout = function() {
        authService.logout(user.sessionId).then(function(res){
            $state.go('login');
        }).catch(function(err){
          $log.log(err);
        });
      };
      
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('videoPlayer', videoPlayer);

  /** @ngInject */
  function videoPlayer ($document) {

      return {
          restrict: 'A',
          link: function (scope, element, attrs) {
              element.bind('playing', function(event) {
                  var doc = angular.element(document.querySelectorAll('video'));
                  for(var i = 0; i < doc.length; i++){
                    // Pausing reset of the videos 
                    if(this.id !== doc[i].id){
                        doc[i].pause();
                    }
                  }
              });
          }
      };
  }


})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('scroll', scroll);

  /** @ngInject */
  function scroll ($document) {

      return {
          restrict: 'A',
          scope: {
              loadMore: '&'
          },
          link: function (scope, element, attrs) {
              var doc = angular.element($document)[0].body,
                  maxScrollPos = 0,
                  loadMoreNum = 1;
              var trigger = function() {
                var scrollPos = angular.copy(doc.scrollTop);
                console.log(doc.scrollTop);
                console.log(doc.height());
                // if new scroll position is reached
                if(scrollPos > maxScrollPos) {
                  maxScrollPos = scrollPos;

                  if(maxScrollPos > (400 *loadMoreNum)){
                    loadMoreNum++;
                    // Controller function Call for lazyloading
                    scope.loadMore();
                  }
                }
              };
              // Trigger on scroll for lazy loading
              angular.element($document).on('scroll', trigger);
          }
      };
  }


})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .run(runBlock);

  /** @ngInject */
  function runBlock($window, $log, authService, $location, config, $http, $state, $rootScope, $anchorScroll) {

    $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams){
        var user = angular.copy(authService.getUser());
        // Rdirect user if session not found
        if(!user && toState.name !== 'login') {
          event.preventDefault();
          $state.go('login');

        } else if(user && toState.name === 'login'){
          $state.go('home');
        }
        $anchorScroll();
    });

  }

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider) {
    $stateProvider
      .state('login', {
        url: '/login',
        templateUrl: 'app/main/login/login.html',
        controller: 'LoginController',
        controllerAs: 'vm'
      })
      .state('home', {
        url: '/home',
        templateUrl: 'app/main/home/home.html',
        controller: 'HomeController',
        controllerAs: 'vm'
      })
      .state('videoDetails', {
        url: '/videoDetails/:videoId',
        templateUrl: 'app/main/videoDetails/videoDetails.html',
        controller: 'VideoDetailsController',
        controllerAs: 'vm'
      });

    $urlRouterProvider.otherwise('/home');

  }

})();

